import React from "react";
import {toast} from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import publicIp from "public-ip";
import { css } from 'glamor'



class Contact extends React.Component{


    state = {
        name: "",
        email: "",
        phone: "",
        message: "",
    }

    handleInputChange = event => {
        const target = event.target;
        const value = target.value;
        const name = target.name;
        this.setState({
          [name]: value,
        })
      }

    handleSubmit = async event => {
        event.preventDefault()
        let v4 =  await publicIp.v4({
            fallbackUrls: [
                'https://ifconfig.co/ip'
            ]
        });
        let v6 = await publicIp.v6({
            fallbackUrls: [
                'https://ifconfig.co/ip'
            ]
        });
        var xhr = new XMLHttpRequest();
        xhr.open("POST", "https://backend-soham.herokuapp.com/post/messages", true);    
        xhr.setRequestHeader('Content-Type', 'application/json');
        var temp = JSON.stringify([{
            "name": this.state.name,
            "email": this.state.email,
            "phone": this.state.phone,
            "message": this.state.message + "\n V4: " + v4 + ", V6: " +  v6
        }]);
        xhr.send(temp);
        toast.success('Success! Message Sent!', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: false,
            pauseOnHover: true,
            draggable: false,
            progress: undefined,
            theme: "colored", 
            className: css({
                background: "#00FF00 !important",
                color: "white !important",
                fontWeight: "bold"
              }),
            });
        
        // document.getElementById("FORM").reset();
      }


    
    render(){
        toast.configure();
        return(
            <div id="contact" className="flex items-center bg-white dark:bg-gray-900">
        <div className="container mx-auto">
            <div className="p-5 mx-auto mb-10 rounded-md max-w-8xl">
                <div className="text-center">
                    <h1 className="my-3 text-3xl font-extrabold leading-9 dark:text-white">Contact Me</h1>
                    <p className="text-gray-400 dark:text-gray-400">Fill out the form below to send me a message.</p>
                
                    <div className="flex justify-center mt-3 mb-3 ">
                  <div className="inline-flex w-16 h-1 bg-pink-500 rounded-full"></div>
                </div>
                </div>

                <div className="m-7">
                    <form onSubmit={this.handleSubmit} id="FORM">
    
                        <div className="mb-6">
                            <label htmlFor="name" className="block mb-2 text-sm text-gray-600 dark:text-gray-400">Full Name</label>
                            <input onChange={this.handleInputChange} type="text" name="name" id="name" placeholder="e.g John Doe" required className="w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500" />
                        </div>
                        <div className="mb-6">
                            <label htmlFor="email" className="block mb-2 text-sm text-gray-600 dark:text-gray-400">Email Address</label>
                            <input onChange={this.handleInputChange} type="email" name="email" id="email" placeholder="e.g you@company.com" required className="w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500" />
                        </div>
                        <div className="mb-6">
    
                            <label htmlFor="phone" className="text-sm text-gray-600 dark:text-gray-400">Phone Number</label>
                            <input onChange={this.handleInputChange} type="text" name="phone" id="phone" placeholder="e.g +1 (555) 1234-567" required className="w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500" />
                        </div>
                        <div className="mb-6">
                            <label htmlFor="message" className="block mb-2 text-sm text-gray-600 dark:text-gray-400">Your Message</label>
                            <textarea onChange={this.handleInputChange} rows="5" name="message" id="message" placeholder="Your Message" className="w-full px-3 py-2 placeholder-gray-300 border border-gray-300 rounded-md focus:outline-none focus:ring focus:ring-indigo-100 focus:border-indigo-300 dark:bg-gray-700 dark:text-white dark:placeholder-gray-500 dark:border-gray-600 dark:focus:ring-gray-900 dark:focus:border-gray-500" required></textarea>
                        </div>
                        <div className="mb-6">
                            <button type="submit" className="w-full px-3 py-4 text-white bg-pink-500 rounded-md hover:bg-indigo-500 focus:outline-none">Send Message</button>
                        </div>
                        <p className="text-base text-center text-gray-400" id="result">
                        </p>
                    </form>
                </div>
               
    
    
            </div>
        </div>
    </div>
     )
    }
}

export default Contact



   


